import gsap from 'gsap';
import ScrollTrigger from 'gsap/dist/ScrollTrigger';
import { exist } from '../../utils';

const socialOrbits = () => {
	const SELECTORS = {
		block: '.js-social-orbit-block',
		el: '.js-social-orbit',
		icon: '.js-social-orbit-icon',
		desktopSvg: '.js-desktop-orbit-svg',
		mobileSvg: '.js-mobile-orbit-svg',
	};

	gsap.registerPlugin(ScrollTrigger);

	const $block = document.querySelector(SELECTORS.block);

	if (!exist($block)) return;

	const elements = $block.querySelectorAll(SELECTORS.el);
	const icon = $block.querySelectorAll(SELECTORS.icon);

	const desktopSvg = $block.querySelector(SELECTORS.desktopSvg);
	const mobileSvg = $block.querySelector(SELECTORS.mobileSvg);

	let mm = gsap.matchMedia();

	const createOrbitAnim = (svg) => {
		const tl = [];
		const svgViewBox = svg.getAttribute('viewBox').split(/\s+/).map(Number);
		const svgOrigin = `${svgViewBox[2] / 2} ${svgViewBox[3] / 2}`;

		elements.forEach((el, index) => {
			tl[index] = gsap.timeline({
				repeat: -1,
				defaults: {
					ease: 'none',
				},
				scrollTrigger: {
					trigger: svg,
					start: 'top bottom',
					end: 'top+=400% top',
					toggleActions: 'play pause play pause',
				},
			});

			let duration = Math.random() * 8 + 20;

			tl[index]
				.to(el, {
					rotation: 360,
					svgOrigin,
					duration,
				})
				.to(
					icon[index],
					{
						rotation: -360,
						transformOrigin: '50% 50%',
						duration,
					},
					'<',
				);
		});
	};

	mm.add('(min-width: 1024px)', () => {
		createOrbitAnim(desktopSvg);
	});

	mm.add('(max-width: 1023px)', () => {
		createOrbitAnim(mobileSvg);
	});
};

export default socialOrbits;
