import gsap from 'gsap';
import ScrollTrigger from 'gsap/dist/ScrollTrigger';
import { exist } from '../../utils';
import { OFFSETS } from '../../utils/constants';

import 'ScssComponents/power.scss';

const powerAnimation = () => {
	gsap.registerPlugin(ScrollTrigger);

	const SELECTORS = {
		section: '.js-power-section',
		image: '.js-power-image',
	};

	const $section = document.querySelector(SELECTORS.section);

	if (!exist($section)) return;

	const $images = $section.querySelectorAll(SELECTORS.image);

	const tl = gsap.timeline({
		paused: true,
		defaults: {
			ease: 'none',
		},
		repeat: -1,
		scrollTrigger: {
			trigger: $section,
			start: 'top bottom',
			end: `bottom+=${OFFSETS.extracted} top`,
			toggleActions: 'play pause play pause',
		},
	});

	const baseDuration = 14;

	tl.fromTo(
		$images[0],
		{
			yPercent: 0,
		},
		{
			yPercent: -100,
			duration: baseDuration,
		},
	);

	$images.forEach((el, index) => {
		if (index !== 0) {
			tl.fromTo(
				el,
				{
					yPercent: 0,
				},
				{
					yPercent: -100,
					duration: baseDuration,
				},
				`-=${baseDuration}`,
			).to(el, {
				yPercent: -200,
				duration: baseDuration,
			});
		}
	});

	tl.fromTo(
		$images[0],
		{
			yPercent: 100,
		},
		{
			yPercent: 0,
			duration: baseDuration,
		},
		`-=${baseDuration}`,
	);
};

export default powerAnimation;
